<template>
    <div class="details container">
        <router-link to="/" class="btn btn-default">返回</router-link>
        <h1 class="page-header">{{customer.name}}
        <span class="pull-right">
            <router-link class="btn btn-primary" v-bind:to="'/edit/'+customer.id">编辑</router-link>
            <button class="btn btn-danger" v-on:click="delectCustomer(customer.id)">删除</button>
        </span>
        </h1>
        <ul class="list-group">
            <li class="list-group-item"><span class="glyphicon glyphicon-asterisk">{{customer.phone}}</span></li>
            <li class="list-group-item"><span class="glyphicon glyphicon-question-sign">{{customer.email}}</span></li>
        </ul>
        <ul class="list-group">
            <li class="list-group-item"><span class="glyphicon glyphicon-asterisk">{{customer.education}}</span></li>
            <li class="list-group-item"><span class="glyphicon glyphicon-question-sign">{{customer.graduationschoool}}</span></li>
            <li class="list-group-item"><span class="glyphicon glyphicon-asterisk">{{customer.profile}}</span></li>
        </ul>
    </div>

</template>

<script>
    export default {
        name: "customerDetails",
        data(){
            return{
                customer:""
            }
        },
        methods:{
            fetchCustomers(id) {
                this.$http.get("http://localhost:3000/user/"+id)
                    .then(function (response) {
                        console.log(response);
                        this.customer=response.body;
                    })
            },
            delectCustomer(id){
                //console.log(id)
                this.$http.delete("http://localhost:3000/user/"+id)
                    .then(function(response){
                        this.$router.push({path:"/"});
                    })
            }
        },
        created(){
            this.fetchCustomers(this.$route.params.id);
        }
    }
</script>

<style scoped>

</style>


